<div class="overflow-auto w-screen h-[100vh] min-h-[100vh] flex flex-col items-center justify-center"><swiper class="w-[80vw] md:w-[35vw]" @swiper="onSwiper" :loop="false" :autoplay="false" :slidesPerView="1" :initialSlide="nowSlide" :speed="1000" :spaceBetween="5"><swiper-slide v-for="(item, index) of lists" :key="item"><div><img class="w-full rounded-[20px]" :src="require(`${item}`)" @click="change"></div></swiper-slide></swiper><swiper class="w-[80vw] md:w-[35vw] mt-5" :loop="false" :autoplay="false" :slidesPerView="5" :initialSlide="0" :speed="1000" :spaceBetween="10"><swiper-slide v-for="(item, index) of lists" :key="item"><div><img class="w-full" :class="[             nowSlide === index ? 'opacity-100' : 'opacity-50',           ]" :src="require(`${item}`)" @click="action(item)"></div></swiper-slide></swiper></div>